
<template>
  <v-container id="login" fill-height>
    <v-row class="text-center" align="center" justify="space-around">
      <v-col class="col-lg-6 col-md-8 mx-auto">
        <v-card flat id="card" elevation="10">
          <v-toolbar dense elevation="0" dark color="primary">
            <v-toolbar-title>AYAME Login</v-toolbar-title>
            <v-progress-linear
              :active="loading"
              :indeterminate="loading"
              absolute
              bottom
              color="deep-purple accent-4"
            ></v-progress-linear>

            <v-spacer></v-spacer>
          </v-toolbar>

          <v-row class="text-center" id="card-item">
            <v-col class="mb-5" cols="12">
              <v-row justify="space-around">
                <v-form ref="form" v-model="valid" lazy-validation>
                  <v-snackbar
                    v-model="snackbar"
                    :timeout="5000"
                    color="warning"
                    class="snackbar-fail"
                  >
                    ログインに失敗しました
                    <v-btn text @click="snackbar = false">Close</v-btn>
                  </v-snackbar>

                  <v-text-field
                    v-model="username"
                    label="ユーザー名"
                    :rules="[v => !!v || 'ユーザー名を入力してください']"
                    required
                  ></v-text-field>

                  <v-text-field
                    v-model="password"
                    label="パスワード"
                    :rules="[v => !!v || 'パスワードを入力してください']"
                    :type="showPassword ? 'text' : 'password'"
                    :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                    @click:append="showPassword = !showPassword"
                    required
                  ></v-text-field>

                  <v-checkbox v-model="checkbox" :rules="[v => !!v || '利用規約に同意してください']" required>
                    <span slot="label">
                      <router-link to="/Terms">利用条件</router-link>に同意します
                    </span>
                  </v-checkbox>

                  <v-btn color="primary" @click="submit">ログイン</v-btn>
                </v-form>
              </v-row>
            </v-col>
          </v-row>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<style>
/* #card{border:solid;border-color:  #763568;border-radius:10px;border-width: 1px;} */

.snackbar-fail {
  color: white;
  font-weight: bolder;
}
/* #login{background-color: ;} */
</style>

<script>
export default {
  name: 'Login',

  components: {},

  methods: {
    fetchtest: function () {
      fetch('/test.json')
        .then(response => response.text())
        .then(data => console.log(data))
    },
    submit: function () {
      if (!this.$refs.form.validate()) {
        return false
      }

      this.loading = true

      const formData = new FormData()
      formData.append('username', this.username)
      formData.append('password', this.password)

      const error = () => {
        this.loading = false
        this.snackbar = true
        console.log(error)
      }

      const success = (data) => {
        console.log('fetch success. storing data')
        this.$store.commit('setToken', data)
        this.$router.push({ path: 'Courses' })
      }

      this.login(formData, success, error)
    }

  },

  mounted: function () {
    this.$store.commit('clearAll')
  },

  data: () => ({
    valid: true,
    username: '',
    password: '',
    checkbox: '',
    snackbar: false,
    loading: false,
    showPassword: false,
    snackbar_color: 'error'
    //
  })
}
</script>
